var sec = 0;
var min = 0;
var timerID;
export var startTimer = function () {
    timer();
};
export var stopTimer = function () {
    var cardFieldTimer = document.querySelector('.gameScreen__timer');
    cardFieldTimer.textContent = '00:00';
    clearTimeout(timerID);
    var finalTime = (min > 9 ? min : '0' + min) + ':' + (sec > 9 ? sec : '0' + sec);
    sec = 0;
    min = 0;
    return finalTime;
};
function tick() {
    sec++;
    if (sec >= 60) {
        sec = 0;
        min++;
    }
}
function add() {
    tick();
    var cardFieldTimer = document.querySelector('.gameScreen__timer');
    cardFieldTimer.textContent =
        (min > 9 ? min : '0' + min) + ':' + (sec > 9 ? sec : '0' + sec);
    timer();
}
function timer() {
    timerID = setTimeout(add, 1000);
}
